import { createRouter, createWebHistory } from 'vue-router';

// Sayfa bileşenlerini içe aktarın
import BulkView from '@/views/BulkView.vue';
import HashlipsOrdinalView from '@/views/HashlipsOrdinalView.vue';
import HomeView from '@/views/HomeView.vue';
import InscriptionsView from '@/views/InscriptionsView.vue';
import MetadabinderView from '@/views/MetadabinderView.vue';
import MetadataextractView from '@/views/MetadataextractView.vue';

const routes = [
  { path: '/', component: HomeView },
  { path: '/bulk', component: BulkView },
  { path: '/hashlips', component: HashlipsOrdinalView },
  { path: '/inscriptions', component: InscriptionsView },
  { path: '/metadabinder', component: MetadabinderView },
  { path: '/metadataextract', component: MetadataextractView },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;