<template>
  <div>
    <input type="file" ref="fileInput" @change="handleFileChange" accept=".json" />
    <button @click="downloadZip" :disabled="jsonData.length === 0">Download as Zip</button>
  </div>
</template>

<script>
import JSZip from 'jszip';

export default {
  data() {
    return {
      jsonData: [],
    };
  },
  methods: {
    handleFileChange() {
      const fileInput = this.$refs.fileInput;
      if (fileInput.files && fileInput.files.length > 0) {
        const reader = new FileReader();
        reader.onload = (e) => {
          try {
            const base64Data = e.target.result.split(',')[1];
            const decodedData = atob(base64Data);
            this.jsonData = JSON.parse(decodedData);
          } catch (error) {
            console.error('Error parsing JSON file:', error);
          }
        };
        reader.readAsDataURL(fileInput.files[0]);
      }
    },
    async downloadZip() {
      const zip = new JSZip();

      // Her bir öğeyi ayrı bir JSON dosyasına çıkart
      this.jsonData.forEach((item, index) => {
        const itemJson = JSON.stringify(item, null, 2);
        zip.file(`${index + 1}.json`, itemJson);
      });

      // Zip dosyasını oluşturun
      const content = await zip.generateAsync({ type: 'blob' });

      // Zip dosyasını indirmek için bir <a> etiketi oluşturun
      const link = document.createElement('a');
      link.href = URL.createObjectURL(content);
      link.download = 'metadata.zip';
      document.body.appendChild(link);

      // <a> etiketini tıklayarak indirme işlemini başlatın
      link.click();

      // <a> etiketini kaldırın
      document.body.removeChild(link);
    },
  },
};
</script>
