<template>
  <v-row>
    <!-- Left side: JSON upload area -->
    <v-col cols="6">
      <v-file-input @change="handleFileUpload(1)" label="First JSON" ref="fileInput1"></v-file-input>
      <v-file-input @change="handleFileUpload(2)" label="Second JSON" ref="fileInput2"></v-file-input>
      <v-btn @click="mergeJson" color="primary">Merge JSON</v-btn>
    </v-col>

    <!-- Right side: Transformed JSON display area -->
    <v-col cols="6">
      <v-card>
        <v-card-title>Transformed JSON</v-card-title>
        <v-card-text>
          <v-textarea v-model="transformedData" readonly></v-textarea>
        </v-card-text>
        
        <v-card-actions class="justify-end">
          <v-btn @click="copyToClipboard" color="primary">Copy</v-btn>
          <v-btn @click="downloadJson" color="success">Download</v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data() {
    return {
      idJsonData: null,
      metaJsonData: null,
      mergedJson: [],
      transformedData: "",
    };
  },
  methods: {
    handleFileUpload(fileIndex) {
      const fileInput = this.$refs[`fileInput${fileIndex}`];
      if (!fileInput.files.length) {
        return; // Stop processing if no file is selected
      }

      const file = fileInput.files[0];

      const reader = new FileReader();

      reader.onload = (e) => {
        const jsonData = JSON.parse(e.target.result);

        if (fileIndex === 1) {
          // Only take the "id" field
          this.idJsonData = jsonData.map((item) => ({ id: item.id }));
        } else if (fileIndex === 2) {
          this.metaJsonData = jsonData;
        }
      };

      reader.readAsText(file);
    },
    mergeJson() {
      // JSON merging operations will be performed here
      this.mergedJson = this.idJsonData.map((idItem, index) => {
        const metaItem = this.metaJsonData[index];
        return { ...idItem, meta: metaItem ? metaItem.meta : null };
      });

      // Convert JSON and assign it to transformedData
      this.transformedData = JSON.stringify(this.mergedJson, null, 2);
      // Prettify the JSON output
      this.transformedData = this.prettify(this.transformedData);
    },

    copyToClipboard() {
      // Clipboard copying operations will be performed here
      navigator.clipboard.writeText(this.transformedData);
    },

    downloadJson() {
      // JSON downloading operations will be performed here
      const blob = new Blob([JSON.stringify(this.mergedJson)], { type: "application/json" });
      const link = document.createElement("a");
      link.href = URL.createObjectURL(blob);
      link.download = "merged_json.json";
      link.click();
    },

    prettify(jsonString) {
      try {
        const jsonObject = JSON.parse(jsonString);
        return JSON.stringify(jsonObject, null, 2);
      } catch (error) {
        console.error('Error prettifying JSON:', error);
        return jsonString;
      }
    },
  },
};
</script>

<style scoped>
/* CSS styles can be added here */
.justify-end {
  justify-content: flex-end;
}
</style>
