<template>
  <v-row>
    <v-col cols="12">
      <v-file-input @change="handleFileUpload" label="Select JSON File"></v-file-input>
    </v-col>

    <v-col v-if="jsonData && jsonData.length" cols="12">
      <v-row>
        <v-col v-for="(item, index) in jsonData" :key="index" cols="4">
          <v-card @click="openModal(item)">
            <v-img :src="getImageUrl(item.id)" height="200" style="image-rendering: pixelated;"></v-img>
            <v-card-title>{{ item.meta && item.meta.name ? item.meta.name : item.id }}</v-card-title>
          </v-card>
        </v-col>
      </v-row>
    </v-col>

    <v-dialog v-model="modal" max-width="600">
      <v-card>
        <v-card-title>Attributes</v-card-title>
        <v-card-text>
          <v-list>
            <v-list-item-group v-if="selectedItem && selectedItem.meta && selectedItem.meta.attributes">
              <v-list-item v-for="(trait, index) in selectedItem.meta.attributes" :key="index">
                <v-list-item-content>
                  <v-list-item-title>{{ trait.trait_type }}</v-list-item-title>
                  <v-list-item-subtitle>{{ trait.value }}</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
            <v-list-item v-if="!selectedItem || !selectedItem.meta || !selectedItem.meta.attributes">
              No attributes available.
            </v-list-item>
          </v-list>
        </v-card-text>
        <v-card-actions>
          <v-btn @click="closeModal" color="primary">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
export default {
  data() {
    return {
      jsonData: null,
      modal: false,
      selectedItem: null,
    };
  },
  methods: {
    handleFileUpload(event) {
      const file = event.target.files[0];

      if (!file) {
        return;
      }

      const reader = new FileReader();

      reader.onload = (e) => {
        const jsonData = JSON.parse(e.target.result);
        this.jsonData = jsonData;
      };

      reader.readAsText(file);
    },
    getImageUrl(id) {
      if (id) {
        return `https://ordinals.com/content/${id}`;
      } else {
        return "";
      }
    },
    openModal(item) {
      this.selectedItem = item;
      this.modal = true;
    },
    closeModal() {
      this.selectedItem = null;
      this.modal = false;
    },
  },
};
</script>

<style scoped>
/* Style definitions can be added here */
</style>
