<template>
  <v-container>
    <v-row>
      <v-col cols="12" md="6">
        <v-checkbox v-model="includeName" label="Include Name"></v-checkbox>
        <v-checkbox v-model="includeHighResImgUrl" label="Include High Res Img Url"></v-checkbox>
        <v-file-input @change="handleFileChange" accept=".json" label="Select JSON File"></v-file-input>
      </v-col>

      <v-col cols="12" md="6">
        <v-card>
          <v-card-title>Transformed JSON</v-card-title>
          <v-card-text>
            <v-textarea v-model="transformedData" readonly></v-textarea>
          </v-card-text>
          <v-card-actions>
            <v-btn @click="copyToClipboard" color="primary">Copy to Clipboard</v-btn>
            <v-btn @click="downloadJson" color="success">Download as JSON</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      includeName: true,
      includeHighResImgUrl: true,
      transformedData: null,
      jsonData: null,
    };
  },
  watch: {
    includeName: 'transformJson',
    includeHighResImgUrl: 'transformJson',
    jsonData: 'transformJson',
  },
  methods: {
    handleFileChange(event) {
      const file = event.target.files[0];

      const reader = new FileReader();
      reader.onload = (e) => {
        try {
          const jsonData = JSON.parse(e.target.result);
          this.jsonData = jsonData;
        } catch (error) {
          console.error('Error parsing JSON file:', error);
        }
      };

      reader.readAsText(file, 'UTF-8');
    },
    transformJson() {
      if (!this.jsonData) return;

      this.transformedData = JSON.stringify(this.jsonData.map(item => {
        const meta = {};

        if (this.includeName && item.name !== undefined) {
          meta.name = item.name;
        }

        if (this.includeHighResImgUrl && item.image !== undefined) {
          meta.high_res_img_url = item.image;
        }

        // Eğer attributes varsa, direkt olarak meta içine ekleyin
        if (item.attributes !== undefined) {
          meta.attributes = item.attributes;
        }

        // Eğer en az bir özellik seçiliyse ve karşılık gelen alan tanımlıysa, meta nesnesini oluştur
        if (Object.keys(meta).length > 0) {
          return {
            meta,
          };
        } else {
          // Eğer hiçbir özellik seçili değilse, boş meta nesnesini kullan
          return {
            meta: {},
          };
        }
      }), null, 2);
    },
    copyToClipboard() {
      const textArea = document.createElement("textarea");
      textArea.value = this.transformedData;
      document.body.appendChild(textArea);
      textArea.select();
      document.execCommand('copy');
      document.body.removeChild(textArea);
    },
    downloadJson() {
      const content = new Blob([this.transformedData], { type: 'application/json;charset=utf-8' });
      const url = URL.createObjectURL(content);
      const a = document.createElement('a');
      a.href = url;
      a.download = 'transformed.json';
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
    },
  },
};
</script>
