<template>
  <v-container>
    <v-file-input label="Choose JSON files" v-model="selectedFiles" multiple @change="handleFileChange" color="white"></v-file-input>
    <v-btn @click="downloadMergedJson" :disabled="selectedJsons.length === 0" color="white">Download Merged JSON</v-btn>
    <v-pre>{{ mergedJson }}</v-pre>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      selectedFiles: [],
      selectedJsons: [],
      mergedJson: null,
    };
  },
  methods: {
    handleFileChange() {
      this.selectedJsons = [];
      const files = this.selectedFiles;
      for (let i = 0; i < files.length; i++) {
        const reader = new FileReader();
        reader.onload = (e) => {
          try {
            const jsonData = JSON.parse(e.target.result);
            this.selectedJsons.push(jsonData);
          } catch (error) {
            console.error('Error parsing JSON file:', error);
          }
        };
        reader.readAsText(files[i]);
      }
    },
    downloadMergedJson() {
      const mergedJsonString = JSON.stringify(this.selectedJsons, null, 2);
      const blob = new Blob([mergedJsonString], { type: 'application/json' });
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = 'merged.json';
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
    },
  },
};
</script>
