<template>
  <v-app>
    <v-container>
      <v-row>
        <!-- Sol Tarafta JSON Düzenleme Formu -->
        <v-col cols="12" md="6">
          <v-form ref="form">
            <v-text-field v-model="formData.name" label="Name"></v-text-field>

            <v-row>
              <v-col cols="12" md="6">
                <v-text-field v-model="formData.slug" label="Slug"></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-text-field v-model="formData.supply" label="Supply"></v-text-field>
              </v-col>
            </v-row>

            <v-text-field v-model="formData.description" label="Description"></v-text-field>
            <v-text-field v-model="formData.inscription_icon" label="Inscription Icon"></v-text-field>

            <v-row>
              <v-col cols="12" md="4">
                <v-text-field v-model="formData.twitter_link" label="Twitter Link"></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field v-model="formData.discord_link" label="Discord Link"></v-text-field>
              </v-col>
              <v-col cols="12" md="4">
                <v-text-field v-model="formData.website_link" label="Website Link"></v-text-field>
              </v-col>
            </v-row>

            <!-- Clear Butonu -->
            <v-btn @click="clearForm" color="error">Clear</v-btn>
          </v-form>
        </v-col>

        <!-- Sağ Tarafta JSON Önizleme ve İndirme -->
        <v-col cols="12" md="6">
          <h2>// meta.json</h2>
          <pre>{{ formattedData }}</pre>
          <v-btn :href="downloadLink" :download="downloadFilename" v-if="downloadLink" color="success">
            Download Beautified JSON
          </v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>

<script>
export default {
  data() {
    return {
      formData: {
        name: "",
        slug: "",
        supply: "",
        description: "",
        inscription_icon: "",
        twitter_link: "",
        discord_link: "",
        website_link: ""
      }
    };
  },
  computed: {
    formattedData() {
      return JSON.stringify(this.formData, null, 2);
    },
    downloadLink() {
      const beautifiedData = encodeURIComponent(JSON.stringify(this.formData, null, 2));
      return `data:application/json;charset=utf-8,${beautifiedData}`;
    },
    downloadFilename() {
      return 'meta.json';
    }
  },
  methods: {
    clearForm() {
      // Form verilerini temizleme
      this.formData = {
        name: "",
        slug: "",
        supply: "",
        description: "",
        inscription_icon: "",
        twitter_link: "",
        discord_link: "",
        website_link: ""
      };
    }
  }
};
</script>

<style>
/* İhtiyaca göre stil ekleyebilirsiniz */
